/*=============================================================================
# GRADUATE STRIPS
==============================================================================*/

.mCSB_container:hover {
  // Enable GPU Accelleration.
  will-change: left;
}

.page-template-graduates {
  .polygon {
    z-index: -5;
  }
  .panel--graduate-strips {
    text-align: left;
    height: 100vh;
    padding: 8rem 3rem 3rem;
    text-align: center;
    &::before {
      content: '';
      display: inline-block;
      height: 100%;
      width: 0;
      vertical-align: middle;
    }
  }
  .graduates-container {
    width: 98%;
    display: inline-block;
    vertical-align: middle;
    text-align: left;


  }
  .strips-container {
    &:hover {

    }
  }

  .strip {
    white-space: nowrap;
    margin: 0 0 1.5rem;
    font-size: 0;
    background: $actualWhite;

    &:nth-child(3){
      margin-bottom: 0;
    }
  }



}


.strip-container, .strip {

  .single-grad {
    display: inline-block;
    width: 15vw;
    // min-height: 150px;
    // max-width: 250px;
    list-style: none;
    position: relative;
    white-space: nowrap;
    background: $actualWhite;
    filter: blur(3px);
    opacity: 0.2;
    transition: all 0.6s;
    &.active {
      filter: none;
      opacity: 1;
    }

    &:nth-of-type(6){
      display: none;
    }

    .grad-container {
      img {
        animation-name: gradFadeIn;
        animation-duration: .3s;
        animation-fill-mode: backwards;
        animation-iteration-count: 1;
        animation-delay: .5s;
        transition: filter 0.4s, opacity 0.4s;
      }


      .grad-name {
        padding: 0.2rem 0.4rem;
        text-align: center;
        position: absolute;
        top: 100%;
        left: 50%;
        color: $actualWhite;
        text-decoration: none;
        font-size: 0.8rem;
        text-transform: uppercase;
        letter-spacing: .2rem;
        font-weight: 500;
        transform: translate(-50%, -50%);
        transition: all .4s;
        .name {
          position: relative;
          z-index: 5;
          transition: opacity 0.2s;
          opacity: 0;
          display: inline-block;

        }
        .board {
          content: '';
          display: block;
          height: 1rem;
          width: 1rem;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%) rotate(45deg);
          background-color: $black;
          box-shadow: 0 0 0.25em rgba(0,0,0,0.1);
          opacity: 0;
          transition: transform 0.3s 0.2s, width 0.2s, height 0.2s, opacity 0.2s;
        }
      }
      &:hover {
        .grad-name {
          top: 75%;
        }

        .name {
          opacity: 1;
          transition: opacity 0.2s 0.4s;
        }
        .board {
          height: 100%;
          width: 100%;
          opacity: 1;
          transform: translate(-50%,-50%) rotate(540deg);
          transition: transform 0.3s, width 0.4s 0.3s, height 0.4s 0.3s, opacity 0.1s;
        }
      }




      img {
        width: 100%;
        height: auto;
      }
    }
  }
}

.panel--fam-strips .single-grad:nth-of-type(6){
  display: none;
}

@media (max-width: 1600px) {
  .strip-container .single-grad {
    width: 24.75%;
  }

  .panel--fam-strips .single-grad:nth-of-type(5),
  .panel--fam-strips .single-grad:nth-of-type(6) {
    display: none;
  }
}

@media (max-width: 1300px) {
  .strip-container .single-grad {
    width: 33.33%;
    margin-bottom: 3rem;
  }

  .panel--fam-strips .single-grad:nth-of-type(5),
  .panel--fam-strips .single-grad:nth-of-type(6) {
    display: inline-block;
  }
}

@media (max-width: 950px) {
  .strip-container .single-grad {
    width: 50%;
    margin-bottom: 3rem;
  }
}

@media (max-width: 600px) {
  .strip-container .single-grad {
    margin-bottom: 2rem;
  }
}

@media (max-width: 360px) {
  .strip-container .single-grad .grad-container .grad-name {
    font-size: .9rem;
  }
}

@keyframes gradFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
